	module.exports = {"data":{"wordpressPost":{"title":"A beauty editor&#8217;s guide to the best of LA","content":"<p><a href=\"http://maryzavaglia.com\">Aussie beauty editor Mary Zavaglia </a>lives and works in the City of Angels &#8211; yes LA. Not surprisingly, given her job and all, she knows the best salons in la la land. Lucky for us she&#8217;s willing to spill the beauty beans &#8211; as it were. Here, her handy LA beauty guide.</p>\n<p><strong>For Facials</strong></p>\n<p><em><strong>Bianca Beverly Hills</strong></em>. This salon specialises in micro-dermabrasion and European facials meaning your skin is super hydrated and baby smooth after just one visit. Bianca is also known for her bespoke facials so expect a truly personalised treatment. Book well in advance though &#8211; she&#8217;s popular &#8211; and also, be prepared to bump into one of the Real Housewives of Beverly Hills. They&#8217;re regulars here.</p>\n<p><strong>Bianca Beverly Hills  </strong><br />\n<strong>Suite #252 </strong><br />\n<strong>462 N Linden Drive, </strong><br />\n<strong>Beverly Hills 90212 </strong><br />\n<strong>(1) 310 489 5121   </strong></p>\n<p>&nbsp;</p>\n<figure id=\"attachment_1581\" style=\"width: 300px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-1581\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14184807/Official-Amy-Poehler-jpg-300x300.jpg\" alt=\"\" width=\"300\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14184807/Official-Amy-Poehler-jpg-300x300.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14184807/Official-Amy-Poehler-jpg-150x150.jpg 150w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14184807/Official-Amy-Poehler-jpg.jpg 597w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">Keep your eyes peeled for comedian Amy Poehler. She frequents several of these salons. Image: Instagram/@officialamypoehler</figcaption></figure>\n<p>&nbsp;</p>\n<p><strong>For Hair Styling</strong></p>\n<p>My all time favourite is <strong><em>Joseph Orozco at Meche</em></strong>. He provides the most precise, most chic haircuts ever. He takes his time though, so book out a morning or afternoon. You won&#8217;t regret it. His cuts are flawless and flattering, plus his blow outs are the bomb. Somehow he makes even the most fine of hair super full and sexy and it stays that way for days. He is definitely my mane man.</p>\n<p><strong>Meche </strong><br />\n<strong>8820 Burton Way  </strong><br />\n<strong>Beverly Hills 90212 </strong><br />\n<strong>(1) 310 278 8930  </strong><br />\n<strong> </strong><br />\n<strong>For Hair Colour</strong></p>\n<p>The colourist who has my heart is<em><strong> Jonathan Gale at Sally Hershburger</strong></em>. He creates the most beautiful, personalised colours so that hair looks healthy and super shiny, yet always 100 per cent natural. He’s the man responsible for Charlize Therons’ glamorous highlights in the Dior J’adore campaigns and also Diane Keaton’s signature sun-kissed, bronde (yep &#8211; that&#8217;s brunette meets blonde for the uninitiated).</p>\n<figure id=\"attachment_1576\" style=\"width: 300px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-1576\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14184132/sally-hershberger-salon-29-1-300x199.jpg\" alt=\"\" width=\"300\" height=\"199\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14184132/sally-hershberger-salon-29-1-300x199.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14184132/sally-hershberger-salon-29-1-768x510.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14184132/sally-hershberger-salon-29-1-1024x681.jpg 1024w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">The super chic Sally H Salon.</figcaption></figure>\n<p><strong>Sally Hershburger </strong></p>\n<p><strong>760 La Cienega Blvd </strong><br />\n<strong>Los Angeles 90069 </strong><br />\n<strong>(1) 310 854 4922 </strong></p>\n<p><strong>For Brows</strong></p>\n<p>I recommend a house call from <strong><em>Hinda Ruenzel</em>. </strong>She whips brows into shape via threading.  Her technique is the best I have ever experienced and my brows look incredibly well-groomed, indeed perfect even, and for weeks. Plus, she has the ability to lift brows effectively taking several years off. I have her on speed dial. Actually, so does half of Beverly Hills!</p>\n<p><strong>(1) 310 7388602</strong></p>\n<p><strong>For Lashes</strong></p>\n<p>My lash queen is<em> <strong>Gloria Ting from Beverly Hills Lashes</strong></em>. She uses lash extensions that are made of a flexible synthetic material (no minks suffered here thank goodness) and also, a superior adhesive meaning those gorgeous lashes stay put for longer.</p>\n<figure id=\"attachment_1582\" style=\"width: 300px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-1582\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14184929/Hollywood-300x150.jpg\" alt=\"\" width=\"300\" height=\"150\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14184929/Hollywood-300x150.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14184929/Hollywood-768x384.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14184929/Hollywood-1024x512.jpg 1024w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">The city of Angels &#8211; and beautiful ones at that. Follow our guide and you&#8217;ll look every bit as good.</figcaption></figure>\n<p>&nbsp;</p>\n<p><strong>Beverly Hills Lashes </strong></p>\n<p><strong>642 ½ N Robertson Blvd </strong></p>\n<p><strong>West Hollywood, CA 90069 </strong></p>\n<p><strong>(1) 310 500 0551  </strong></p>\n<p><strong>For Manicures and Pedicures</strong></p>\n<p>My go to is <em><strong>Rosemary Pittman at Umberto Beverly Hills</strong></em>. Her nail polish application is an art in itself. Plus, her manicures last up to 10 days &#8211; yes, it’s true! Also her pedicures are to die for. They look gorgeous plus come with a serious exfoliation treatment and a deeply relaxing massage.</p>\n<p><strong>Umberto Beverly Hills </strong><br />\n<strong>416 N Canon Drive  </strong><br />\n<strong>Beverly Hills, CA 90210 </strong><br />\n<strong>(1) 3102746395  </strong></p>\n<p><strong>For Spa Treats</strong></p>\n<p>If you&#8217;re up for some serious pampering, head straight to the <em><strong>Ole Henriksen Spa.</strong></em> I recommend the Taste of Indulgence package. It includes a hydrotherapy soak in water with a hint of French lavender. This is followed by an aromatic salt scrub followed by a sumptuous massage with your choice of rich body oil or a hydrating lotion. Then, there&#8217;s a tailored facial treatment to leave your complexion glowing followed by a manicure and pedicure. Heaven.</p>\n<figure id=\"attachment_1577\" style=\"width: 300px\" class=\"wp-caption alignnone\"><img class=\"wp-image-1577 size-medium\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14184309/Ole-H-300x300.jpg\" alt=\"\" width=\"300\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14184309/Ole-H-300x300.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14184309/Ole-H-150x150.jpg 150w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14184309/Ole-H.jpg 450w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">Ole has his very own range too &#8211; you&#8217;ll definitely be using that at the spa.</figcaption></figure>\n<p>&nbsp;</p>\n<p><strong>Ole Henriksen </strong><br />\n<strong>8622A West Sunset Boulevard </strong><br />\n<strong>Los Angeles, CA 90069 </strong><br />\n<strong>(1) 310 854 7700 </strong></p>\n<p><strong>For Hair Removal</strong></p>\n<p>My favourite type of hair removal is waxing and nobody does it better than <em><strong>Jannet Targosz at Wax</strong></em>. She uses a combination of hard and strip wax making sure not to irritate the skin giving you a less painful experience. Yay for that. Jannet also prepares the area with a numbing spray and finishes with a massage application of aloe vera and antiseptic spray.</p>\n<p><strong>WAX </strong><br />\n<strong>7922 Melrose Ave </strong><br />\n<strong>Los Angeles, CA 90046 </strong><br />\n<strong>(1) 323 9519616 </strong></p>\n<p><strong>For Faux Tans</strong></p>\n<p><em><strong>Fiona Locke</strong></em> is one of the most sought-after skin finishing and custom tanning experts in the world. She is known for somehow being able to provide a truly natural-looking, golden glow. Fiona calls her technique &#8216;skin finishing&#8217; because it involves bronzing, but also includes contouring to add definition to the body. Hello abs! Her celebrity clients include Kim Kardashian, Amy Poehler, Evangeline Lilly and Olivia Wilde.</p>\n<figure id=\"attachment_1579\" style=\"width: 300px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-1579\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14184625/Kim-Jpg-300x203.jpg\" alt=\"\" width=\"300\" height=\"203\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14184625/Kim-Jpg-300x203.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14184625/Kim-Jpg.jpg 596w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">Kim Kardashian at her shiny best. Maybe you&#8217;ll see her? Image: Instagram/@kimkardashian</figcaption></figure>\n<p>&nbsp;</p>\n<p><strong>Fiona Locke Studio </strong><br />\n<strong>1431 Ocean Avenue  </strong><br />\n<strong>Santa Monica CA 90401 </strong><br />\n<strong>(1) 561-371-4356 </strong></p>\n<p><strong>For Makeup</strong></p>\n<p>The best make-up is via the folk at <em><strong>Violet Grey</strong></em>. Your fully qualified make-up artist (who may have just come from a private visit with a Hollywood A-Lister) will give you an in-depth consultation and create a stunning make-up look that will make you look more beautiful than ever. We&#8217;re talking make-up miracle workers!</p>\n<p><strong>Violet Grey </strong><br />\n<strong>8452 Melrose Place </strong><br />\n<strong>Los Angeles, CA 90069 </strong><br />\n<strong>(1) 323 782 9700 </strong></p>\n<p><strong>My Expert tips…  </strong><br />\nI recommend regular facials and also micro-dermabrasion<strong> to keep skin looking younger for longer</strong>.</p>\n<p>Your hair is your crowning glory and should be treated as such. I like applying a <strong>weekly conditioning treatment</strong>. For a mega-watt shine add a few drops of a tone-on-tone hair colour to the treatment (making sure it’s a colour match) and leave for 15 minutes. Rinse well and finish with a cool rinse.</p>\n<p>Remember to slather on a <strong>daily body moisturiser</strong>  &#8211; a beauty step that is often forgotten. Focus on elbows, knees and feet. A smooth and sexy sheen all over is a definite head turner!</p>\n<p>And lastly, <strong>smile and laugh</strong> a lot. It really will radiate your inner beauty!</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/04/14185210/Olivia-Wilde-jpg.jpg"},"categories":[{"slug":"beauty-talk","name":"Beauty Talk"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"f44d14c7-9cf0-5007-af74-07484fa1eb5e"}}

